/* @mixin font($fw: 900){
    font-family: 'Lato';
    font-weight: $fw;
 } */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

@media (max-width: 1199.98px) {
  #fetures-section .fetures-place span {
    font-size: 14px;
    line-height: 19px; } }

@media (max-width: 575.98px) {
  #fetures-section .fetures-place span {
    font-size: 11px;
    line-height: 14px;
    height: 60px; } }

.about {
  padding: 100px 0; }
  @media (max-width: 991.98px) {
    .about {
      padding: 50px 0 60px; } }
  @media (max-width: 767.98px) {
    .about {
      padding: 20px 0 50px; } }
  @media (max-width: 575.98px) {
    .about {
      padding: 10px 0 40px; } }
  .about__img {
    width: 100%; }
  .about__row {
    display: flex;
    justify-content: center;
    align-items: center; }
    @media (max-width: 991.98px) {
      .about__row {
        justify-content: flex-start;
        align-items: flex-start; } }
    @media (max-width: 767.98px) {
      .about__row {
        justify-content: center;
        align-items: center; } }

.content-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 50px; }
  @media (max-width: 1199.98px) {
    .content-header {
      margin-bottom: 35px; } }
  @media (max-width: 767.98px) {
    .content-header {
      margin-bottom: 25px; } }
  @media (max-width: 575.98px) {
    .content-header {
      margin-bottom: 20px; } }
  .content-header__line {
    width: 100px;
    height: 10px;
    background: #55C8E8;
    margin-right: 50px; }
    @media (max-width: 1199.98px) {
      .content-header__line {
        height: 5px;
        width: 80px;
        margin-right: 40px; } }
    @media (max-width: 767.98px) {
      .content-header__line {
        height: 3px;
        width: 60px;
        margin-right: 25px; } }
    @media (max-width: 575.98px) {
      .content-header__line {
        height: 2px;
        width: 50px;
        margin-right: 20px; } }
  .content-header__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 52px;
    color: #1C3554;
    margin-bottom: 0; }
    @media (max-width: 1199.98px) {
      .content-header__title {
        font-size: 35px;
        line-height: 39px; } }
    @media (max-width: 767.98px) {
      .content-header__title {
        font-size: 26px;
        line-height: 30px; } }
    @media (max-width: 575.98px) {
      .content-header__title {
        font-size: 20px;
        line-height: 24px; } }

.content__subtitle {
  font-size: 35px;
  line-height: 45px;
  color: #000;
  margin-bottom: 50px; }
  @media (max-width: 1199.98px) {
    .content__subtitle {
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 35px; } }
  @media (max-width: 767.98px) {
    .content__subtitle {
      margin-bottom: 25px;
      font-size: 20px;
      line-height: 30px; } }
  @media (max-width: 575.98px) {
    .content__subtitle {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 20px; } }

.content__text {
  font-size: 18px;
  line-height: 30px;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 50px; }
  @media (max-width: 1199.98px) {
    .content__text {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 35px; } }
  @media (max-width: 767.98px) {
    .content__text {
      margin-bottom: 25px;
      font-size: 13px;
      line-height: 24px; } }
  @media (max-width: 575.98px) {
    .content__text {
      font-size: 13px;
      line-height: 21px;
      margin-bottom: 20px; } }

.content__buttons {
  display: flex;
  justify-content: space-between;
  width: 300px; }
  @media (max-width: 1199.98px) {
    .content__buttons {
      width: 250px; } }
  @media (max-width: 767.98px) {
    .content__buttons {
      width: 200px; } }

.content__btn {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  background: #55C8E8;
  border-radius: 8px;
  padding: 15px 25px;
  border: 1px solid transparent;
  transition: 0.6s;
  display: inline-block; }
  @media (max-width: 1199.98px) {
    .content__btn {
      font-size: 14px;
      padding: 10px 20px;
      border-radius: 5px; } }
  @media (max-width: 767.98px) {
    .content__btn {
      font-size: 12px;
      padding: 7px 14px; } }
  .content__btn:hover {
    background: #fff;
    border-color: #55C8E8;
    color: #55C8E8; }

.work {
  padding-bottom: 100px; }
  @media (max-width: 991.98px) {
    .work {
      padding-bottom: 60px; } }
  @media (max-width: 575.98px) {
    .work {
      padding-bottom: 40px; } }

@media (max-width: 991.98px) {
  .col__img {
    display: none; } }
